    font-family: 'Rocksalt';
    text-align: center;
    color: #ffffff;
}

.menu-list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 30px;
    padding: 50px;
}

.card {
    border-right: 1px solid #ffffff;
    padding: 20px;
    display: grid;
    grid-template-columns: 1fr 2fr;
    gap: 15px;
}

.card img {
    grid-column: 1 / 2;
    width: 100%;
    border-radius: 15px;
}

.item-info {
    grid-column: 2 / 3;
    display: flex;
    flex-direction: column;
    gap: 15px;
    font-family: 'Montserrat';
}

.item-info h3 {
    color: #ffffff;
}

.item-info span {
    color: #dfa335;
    align-self: flex-end;
}

.item-info p {
    color: #b3afaf;
    text-align: justify;
}

.item-info button {
    color: #000000;
    font-weight: bold;
    border-style: inset;
    background-color: #dfa335;
    width: fit-content;
    padding-top: 5px;
    padding-bottom: 5px;
    padding-right: 10px;
    padding-left: 10px;
    align-self: self-end;
}`, "",{"version":3,"sources":["webpack://./src/css/bill.css"],"names":[],"mappings":"AAAA;IACI,uBAAuB;IACvB,kBAAkB;IAClB,cAAc;AAClB;;AAEA;IACI,aAAa;IACb,qCAAqC;IACrC,SAAS;IACT,aAAa;AACjB;;AAEA;IACI,+BAA+B;IAC/B,aAAa;IACb,aAAa;IACb,8BAA8B;IAC9B,SAAS;AACb;;AAEA;IACI,kBAAkB;IAClB,WAAW;IACX,mBAAmB;AACvB;;AAEA;IACI,kBAAkB;IAClB,aAAa;IACb,sBAAsB;IACtB,SAAS;IACT,yBAAyB;AAC7B;;AAEA;IACI,cAAc;AAClB;;AAEA;IACI,cAAc;IACd,oBAAoB;AACxB;;AAEA;IACI,cAAc;IACd,mBAAmB;AACvB;;AAEA;IACI,cAAc;IACd,iBAAiB;IACjB,mBAAmB;IACnB,yBAAyB;IACzB,kBAAkB;IAClB,gBAAgB;IAChB,mBAAmB;IACnB,mBAAmB;IACnB,kBAAkB;IAClB,oBAAoB;AACxB","sourcesContent":[".page-title {\n    font-family: 'Rocksalt';\n    text-align: center;\n    color: #ffffff;\n}\n\n.menu-list {\n    display: grid;\n    grid-template-columns: repeat(2, 1fr);\n    gap: 30px;\n    padding: 50px;\n}\n\n.card {\n    border-right: 1px solid #ffffff;\n    padding: 20px;\n    display: grid;\n    grid-template-columns: 1fr 2fr;\n    gap: 15px;\n}\n\n.card img {\n    grid-column: 1 / 2;\n    width: 100%;\n    border-radius: 15px;\n}\n\n.item-info {\n    grid-column: 2 / 3;\n    display: flex;\n    flex-direction: column;\n    gap: 15px;\n    font-family: 'Montserrat';\n}\n\n.item-info h3 {\n    color: #ffffff;\n}\n\n.item-info span {\n    color: #dfa335;\n    align-self: flex-end;\n}\n\n.item-info p {\n    color: #b3afaf;\n    text-align: justify;\n}\n\n.item-info button {\n    color: #000000;\n    font-weight: bold;\n    border-style: inset;\n    background-color: #dfa335;\n    width: fit-content;\n    padding-top: 5px;\n    padding-bottom: 5px;\n    padding-right: 10px;\n    padding-left: 10px;\n    align-self: self-end;\n}"],"sourceRoot":""}]);
